import {
  Directive,
  Input,
  OnChanges,
  SimpleChanges,
  TemplateRef,
  ViewContainerRef
} from '@angular/core';

@Directive({
  selector: '[appHashKeys]'
})
export class HashKeysDirective implements OnChanges {
  // let item of items ${selector}Ofinput
  // 
  @Input()
  appHashKeysOf: any;

  constructor(
    // 
    private viewContainerRef: ViewContainerRef,
    // appHashKeys
    private templateRef: TemplateRef<any>
  ) {}

  ngOnChanges(changes: SimpleChanges) {
    // DOM
    this.viewContainerRef.clear();
    // 
    const keys = Object.keys(this.appHashKeysOf);
    keys.forEach(key => {
      // 
      this.viewContainerRef.createEmbeddedView(this.templateRef, {
        // let item of itemsitem$implicit
        // ngForOfindex,first,last,even,odd
        $implicit: key
      });
    });
  }
}
